.cncium__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem;
}


.cncium__navbar-links {
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.cncium__navbar-links_logo {
    margin-right: 2rem;
}

.cncium__navbar-links_logo img {
    width: 62.56px;
    height: 16.02px;
}

.cncium__navbar-links_container {
    display: flex;
    flex-direction: row;

}

.cncium__navbar-contact {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.cncium__navbar-links_container p,
.cncium__navbar-contact p,
.cncium__navbar-menu p {
    color: #FFF; 
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px; 
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
}

.cncium__navbar-contact button,
.cncium__navbar-menu_container button {
    padding: 0.5rem 1rem;
    color: #FFF;
    background: #FF4820;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border-radius: 5px;
    border: 0;
    outline: 0;
    cursor: pointer;
}


.cncium__navbar-menu {
    margin: 1rem;
    display: none;
    position: relative;
}

.cncium__navbar-menu svg {
    cursor: pointer;
}

.cncium__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0,0,0,0.2);
}

.cncium__navbar-menu_container p {
    margin: 1rem 0;
}

.cncium__navbar-menu_container-links-contact {
    display: none;
}

@media screen and (max-width: 1050px) {
    .cncium__navbar-links_container {
        display: none;
    }
    .cncium__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .cncium__navbar {
        padding: 2rem 4rem;
    }
    .cncium__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 550px) {
    .cncium__navbar {
        padding: 2rem;
    }
    .cncium__navbar-menu {
        display: flex;
    }
    .cncium__navbar-contact {
        display: none;
    }
    .cncium__navbar-menu_container {
        top:20px;
    }
    .cncium__navbar-menu_container-links-contact {
        display: block;
    }
}


* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

body {
    background: var(--color-bg);
}

a {
    color: unset;
    text-decoration: none;
}

.gradient__bg {
  
    /* safari 5.1+,chrome 10+ */
  
    /* opera 11.10+ */
  
    /* ie 10+ */
  
    /* global 92%+ browsers support */
    background:radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 25%);
  }
  
  .gradient__text {
    background: var(--gradient-text);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  
  .section__padding {
    padding: 4rem 6rem;
  }
  
  .section__margin {
    margin: 4rem 6rem;
  }
  
  .scale-up-center {
      -webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
      animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
  }
  
  @-webkit-keyframes scale-up-center {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  
  @keyframes scale-up-center {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  
  @media screen and (max-width: 700px) {
    .section__padding {
      padding: 4rem;
    }
  
    .section__margin {
      margin: 4rem;
    }
  }
  
  @media screen and (max-width: 550px) {
    .section__padding {
      padding: 4rem 2rem;
    }
  
    .section__margin {
      margin: 4rem 2rem;
    }  
  }
